import React, { Component } from 'react';
import Contact from './Contact';
import bgd from './img/smartphone.svg';

class Contacts extends Component {

    state = {
        contacts: [{
            firstName: "Барней",
            lastName: "Стинсовський",
            phone: "+380956319521",
            gender: "male"
        }, {
            firstName: "Робін",
            lastName: "Щербатська",
            phone: "+380931460123",
            gender: "female"
        }, {
            firstName: "Анонімний",
            lastName: "Анонімус",
            phone: "+380666666666",
            gender: "undefined"
        }, {
            firstName: "Лілія",
            lastName: "Олдровна",
            phone: "+380504691254",
            gender: "female"
        }, {
            firstName: "Маршен",
            lastName: "Еріксонян",
            phone: "+380739432123",
            gender: "male"
        }, {
            firstName: "Теодор",
            lastName: "Мотсбес",
            phone: "+380956319521",
            gender: "male"
        }],
        search: '',
    };
    handleSearchChange = e => {
        this.setState({ search: e.target.value });
    };

    render() {
        const filteredContacts = this.state.contacts.filter(e =>
            e.lastName.toLowerCase().includes(this.state.search.toLowerCase())
            || e.firstName.toLowerCase().includes(this.state.search.toLowerCase()) ||
            e.phone.toLowerCase().includes(this.state.search.toLowerCase()) ||
            e.gender.toLowerCase().includes(this.state.search.toLowerCase()));

        if (this.state.search === (!this.state.search)) {
            filteredContacts = this.state.contacts;
        } else {
            filteredContacts.map((contact) => <Contact {...contact} key={contact.index} />);
        }

        const maleCheck = document.querySelector('#male');
        const femaleCheck = document.querySelector('#female');
        const undefCheck = document.querySelector('#undefined');

        function sortByCheckbox() {
            return filteredContacts.map(e => {
                if (e.gender === 'male' && maleCheck.onClick) {
                    filteredContacts.splice(0, 4, 5);
                } else if (e.gender === 'female' && femaleCheck.onClick) {
                    filteredContacts.splice(1, 3);
                } else if (e.gender === 'undefined' && undefCheck.onClick) {
                    filteredContacts.splice(2);
                }
            });
        };




        return (
            <section className="contacts-wrapper">
                <img className="bgd-img" src={bgd}></img>
                <div className="inner-phone-content">
                    <div className="search-wrapper">
                        <input type="text" placeholder="Input name/gender/phone"
                            value={this.state.search} onChange={this.handleSearchChange}
                            className="input"></input>
                    </div>
                    <div className="checkboxes-wrapper">
                        <span>male</span><input type="checkbox" id="male" checked="cheked" onClick={sortByCheckbox, this.checked = "disable"}></input>
                        <span>female</span><input type="checkbox" id="female" checked="checked" onClick={sortByCheckbox, this.checked = "disable"}></input>
                        <span>undefined</span><input type="checkbox" id="undefined" checked="checked" onClick={sortByCheckbox, this.checked = "disable"}></input>
                    </div>
                    {filteredContacts.map((contact) => <Contact {...contact} key={contact.index} />)}
                </div>
            </section>
        )
    }

}

export default Contacts;